.switch {
  position: relative;
  right: 0;
  display: inline-block;
  width: 110px;
  height: 34px;
  font-size: 20px; }

.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #76cfec;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  display: flex;
  align-items: center;
  justify-content: center; }

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  z-index: 2;
  background-color: white;
  -webkit-transition: 0.7s;
  transition: 0.7s; }

.slider:after {
  content: attr(data-off);
  color: #22577d;
  position: absolute;
  padding-left: 20px;
  transition: all 0.7s; }

input:checked + .slider {
  background-color: #b8e0ed; }

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3; }

input:checked + .slider:before {
  -webkit-transform: translateX(76px);
  -ms-transform: translateX(76px);
  transform: translateX(76px); }

input:checked + .slider:after {
  content: attr(data-on);
  color: #22577d;
  padding-left: 0;
  padding-right: 30px; }

/* Rounded sliders */
.slider.round {
  border-radius: 34px; }

.slider.round:before {
  border-radius: 50%; }
